<template>
<el-container>
  <el-main>
    <el-row>
      <el-col :span="18">
        <div class="table">
        <el-table
    :data="tableData"
    :header-cell-style="setHeaderStyle"
    border
      height="485"
        stripe
    style="width: 100%">
    <el-table-column
      label=""
       class-name="text"
      width="135">
        <template slot-scope="scope">
        <div class="center text">{{ scope.row.category}}</div>
      </template>
    </el-table-column>
    <el-table-column
    class-name="desc text"

      label=""
      width="560">

          <template slot-scope="scope">

        <span style="white-space: pre-line">{{ scope.row.desc}}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
       class-name="text">
      
        <template slot-scope="scope">
        <div class="center">{{ scope.row.total}}</div>
      </template>
    </el-table-column>
  </el-table>
        </div>
      </el-col>

      <el-col :span="6">
        <div class="img">
        <el-progress type="circle"  :percentage="total"
        :stroke-width="15"
        :width="200"
        :color="text.color"
        ></el-progress>
         <br>
        <br>
        <div class="resDesc">
          {{text.txt}}
        </div>
        </div>

      </el-col>
    </el-row>

<el-dialog
  :visible.sync="dialogVisible"
  width="30%"
  :close="false"
  :show-close="false"
  >
     <el-result icon="success" title="" subTitle="">
    </el-result>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="finish"> </el-button>
  </span>
</el-dialog>

  </el-main>

  <el-footer>
  </el-footer>
  <el-row style="padding: 0 20px">
    <el-col :span="12" class="left">
      <el-button @click="backToIndex" plain type="primary"> </el-button>
    </el-col>

    <el-col :span="12" class="right">
      <el-button @click="dialogVisible = true" type="primary">  </el-button>
    </el-col>
  </el-row>

  </el-container>
</template>

<script>
export default {
  name: 'Result',
  computed: {
    tableData: function () {
      return this.content.map((i, index) => {
        const key = this.names[index]
        const score = this.$store.state.scores[key]
        const data = '' + score + '/' + i.total
        return {category: i.category, total: data, desc: i.desc.join('\n')}
      })
    },
    total: function () {
      const names = Object.keys(this.$store.state.scores)
      let res = 0
      names.forEach((n) => {
        res += this.$store.state.scores[n]
      })
      return res
    },
    text: function () {
      let res
      this.textData.forEach((i) => {
        if (this.total >= i.min && this.total < i.max) {
          res = i
        }
      })
      return res
    }
  },

  data () {
    return {
      dialogVisible: false,
      textData: [{
        txt: ' ~',
        min: 0,
        max: 60,
        color: '#E6A23C'
      },
      {
        txt: ' ~',
        min: 60,
        max: 85,
        color: '#409EFF'
      }, {
        txt: ' ',
        min: 85,
        max: 100,
        color: '#67C23A'
      },
      {
        txt: ' ',
        min: 100,
        max: 101,
        color: '#F56C6C'
      }

      ],
      names:
  ['historyCollect',
    'planDiscuss',
    'preparation',
    'remove',
    'plantTest',
    'plantExp',
    'warnings'],
      content: [{
        category: '',
        desc: [ '1. ,', '2. CBCT,' ],
        total: 4.5
      },
      {
        category: '',
        desc: [ '1. ', '2. ,', '3. ,', '4. ,', '5. 21', '' ],
        total: 10.5
      },
      {
        category: '',
        desc: ['1. 2-3', '2. ,,', '3. ,4%+1/100,000', '4. '],
        total: 7.5
      },
      {
        category: '',
        desc: [ '1. 0.5%', '2. ', '3. ' ],
        total: 4.5
      },
      {
        category: '',
        desc: [ '1. ,2mm', '2. ', '3. ,', '4. ' ],
        total: 6
      },
      {
        category: '',
        desc: [ '1. ,,,,2.83.6,', '2. ,,', '3. ,,,3D,,10~15N' ],
        total: 61
      },
      {
        category: '',
        desc: [ '1. ,', '2. ,', '3. ' ],
        total: 6
      }
      ]

    }
  },
  methods: {
    setHeaderStyle: function () {
      return {'text-align': 'center'}
    },
    backToIndex: function () {
      this.$store.commit('reset')
      this.$router.push('/')
    },
    finish: function () {
      this.dialogVisible = false
      this.backToIndex()
    }
  }
}
</script>

<style scoped>
  @import "../assets/style.css";
  .name{
    color: white;
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .right{
    text-align: end;
  }

  .resDesc{
    font-size: large;
  }

</style>



