<template>
  <div>
    <el-container>
      <el-main>
<el-steps :active="subIndex" finish-status="success" align-center>
  <el-step v-for="title in titles" :title="title" :key="title"></el-step>
</el-steps>
<br>
<keep-alive>
    <Chat v-if="subIndex === 0" @enableNext="enableNext"/>
</keep-alive>
    <div v-if="subIndex === 1">
      <div class="left">
      <i class='el-icon-arrow-right' />)
          <i  v-if="normalCheckRes && submittedNormal" class="el-icon-circle-check right" />
        <i  v-if="!normalCheckRes && submittedNormal" class="el-icon-circle-close wrong" />
      </div>
      <br>
      <div class="left options">
      <el-row>
        <div>
      <el-checkbox v-model="checked" label="1" :disabled="submittedNormal" class="form">
        
      </el-checkbox>
      <div  class="tips" v-if="checked.indexOf('1') > -1">
        21+
      </div>
        </div>
      </el-row>
      <el-row>
  <el-checkbox v-model="checked" label="2" :disabled="submittedNormal" class="form"></el-checkbox>
  <div  class="tips" v-if="checked.indexOf('2') > -1">
        
      </div>

      </el-row>

<el-row>
    <el-checkbox v-model="checked" label="3" :disabled="submittedNormal" class="form"></el-checkbox>
      <div  class="tips" v-if="checked.indexOf('3') > -1">
       36mm
      </div>
</el-row>
      </div>
      <br>
      <br>

      <el-button @click="submitNormal" v-if="!submittedNormal" type="primary"> </el-button>
    </div>
    <keep-alive>
    <History v-if="subIndex === 2" @enableNext="fillForm"/>
    </keep-alive>
    <div v-if="subIndex === 3">
      <div class="left">
      <i class='el-icon-arrow-right' />():
      <i  v-if="radio === '5' && submittedFilm" class="el-icon-circle-check right" />
        <i  v-if="radio !== '5' && submittedFilm" class="el-icon-circle-close wrong" />
      </div>
      <br>
      <div class="left" v-if="!submittedFilm">
      <el-row class="row">
        <el-radio v-model="radio" label="1" class="form"></el-radio>
      </el-row>
      <el-row class="row">
          <el-radio v-model="radio" label="2" class="form"></el-radio>
      </el-row>
      <el-row class="row">
           <el-radio v-model="radio" label="3" class="form"></el-radio>
      </el-row>
      <el-row class="row">
           <el-radio v-model="radio" label="4" class="form"></el-radio>
      </el-row>
      <el-row class="row">
             <el-radio v-model="radio" label="5" class="form">CBCT</el-radio>
      </el-row>

      </div>
            <div class="tips" v-if="submittedFilm">
              CBCT211.2mm
              <img id="img" src="https://imgs-videos.oss-cn-beijing.aliyuncs.com/CBCT.jpg">
            </div>
    <br>
<br>
<el-button @click="submitFilm" v-if="!submittedFilm" type="primary"></el-button>

    </div>

      <el-dialog
      :title="''"
  :visible.sync="dialogVisible"
  width="30%">
  <span slot="title" class="dialogTitle"></span>
  <div class="text">211/3</div>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
</el-dialog>
</el-main>

    <el-footer>
<Footer :subIndex="subIndex" :maxSubIndex="3" :nextEnabled="nextEnabled" @goNextSubIndex="goNext"  @goPrevSubIndex="goPrev"/>
    </el-footer>
    </el-container>
  </div>

</template>

<script>
import Chat from './collect/Chat'
import History from './collect/History'
import Footer from './Footer'
export default {
  name: 'Collect',
  components: {Chat, History, Footer},
  data () {
    return {
      nextEnabled: false,
      subIndex: 0,
      submittedFilm: false,
      normalCheckRes: false,
      submittedNormal: false,
      filmRes: undefined,
      radio: null,
      checked: [],
      titles: ['', '', '', ''],
      dialogVisible: false,
      filledForm: false
    }
  },
  watch: {
    submittedNormal: function () {
      this.nextEnabled = true
    },
    submittedFilm: function () {
      this.nextEnabled = true
    },
    normalCheckRes: function () {
      this.$store.commit('addScore', {partName: 'historyCollect', score: 3})
    },
    filmRes: function (val) {
      if (val === '5') {
        this.$store.commit('addScore', {partName: 'historyCollect', score: 1.5})
      }
    }
  },
  methods: {
    enableNext: function () {
      this.nextEnabled = true
    },
    goPrev: function () {
      this.subIndex--
      this.nextEnabled = true
    },
    goNext: function () {
      if (this.subIndex === 3) {
        this.dialogVisible = true
      } else {
        if (this.subIndex + 1 === 1 && !this.submittedNormal) {
          this.nextEnabled = false
        }
        if (this.subIndex + 1 === 2 && !this.filledForm) {
          this.nextEnabled = false
        }
        if (this.subIndex + 1 === 3 && !this.submittedFilm) {
          this.nextEnabled = false
        }
        return this.subIndex++
      } ;
    },
    enter: function () {
      this.$router.push('/test')
    },
    submitNormal () {
      this.$data.submittedNormal = true

      if (this.$data.checked.length < 3) {
        this.$data.normalCheckRes = false
        this.$data.checked = ['1', '2', '3']
        return
      }
      this.$data.normalCheckRes = true
    },
    submitFilm () {
      this.$data.submittedFilm = true
      this.filmRes = this.radio
    },
    fillForm () {
      this.filledForm = true
      this.nextEnabled = true
    }

  }
}
</script>

<style>
  @import "../assets/style.css";
  span{
    word-break:break-all;
  }
  .options{
    font-size: xx-large;
  }
  .tips{
    font-size: large;
    color: cornflowerblue;

  }
  .tab{
    margin-left: 25px;
  }

  #img{
    width: 500px;
  }

</style>



